<template>
  <div class="text_study">
    <div class="header" v-show="isShowBottom">
      <div class="back"  @click.stop="back()">
        <img src="../../assets/image/联合42.png" alt="" v-if="showTy">
        <img src="../../assets/image/夜间返回.png" alt="" v-if="!showTy">
      </div>
      <div class="styleHead"><span>{{nameMes}}</span></div>
    </div>
    <div>
      <v-touch  v-show="isSwiper" v-on:tap="changeType"   v-on:swipeleft="swiperleft" v-on:swiperight="swiperright" class="wrapper" ref='wrapper' style="background:#fff;position:fixed;top:0;left:0;width:100%; ">
        <div class="touchConteiner" ref="menuContainer" style="border:none;" >
          <div class='columnTop'>
            <span class='columnChapter' style="margin-left:0.39rem;"></span>
            <span class='columnTitle'></span>
          </div>
          <div class='column' id='column' style='position:absolute;top:0.98rem;left:0;z-index:10;'>
            <div class='columnContainer'></div>
          </div>
          <div class='column2' id='column2' style='position:absolute;top:0.98rem;left:0;z-index:-2;opacity: 0;'>
            <div class='columnContainer2'></div>
          </div>
        </div>

      </v-touch>

    </div>
     <div class="jbjClass" :class="{colorClass: !isColor}" v-if="isShowBottom" style="min-height: 1.05rem;width: 100%;bottom: 0.98rem;position: absolute;display: flex;align-items: center;" @click="jbj()">
       <img style="width:0.33rem;height:0.33rem;position: absolute;margin-left:0.34rem;top: 0.35rem;" src="../../assets/image/记录@2x.png">
       <span style="font-size:0.28rem;color:rgba(159,167,175,0.8);margin-left:0.81rem;">记笔记...</span>
     </div>

     <div v-if="!isShowBottom "  :class="{colorClassTwo: !isColor}"  style="height: 1.05rem;width: 100%;bottom: 0.98rem; position: absolute;display: flex;border:none!important;">
    </div>
    <div class='bottomShow' v-if="isShowBottom" :class="{colorClass: !isColor}">
      <div class='icon' >
        <img v-if="!showTy" src="../../assets/image/目录夜间.png" alt="" class='catalogueIcon' @click="open(1)">
        <img v-if="showTy" src="../../assets/image/目录.png" alt="" class='catalogueIcon' @click="open(0)">
      </div>
      <div class='icon' >
        <img v-if="showTy" src="../../assets/image/太阳@2x.png" alt="" class='catalogueIcontwo' @click="opentwo(0)">
        <img v-if="!showTy" src="../../assets/image/月亮@2x.png" alt="" class='catalogueIcontwo' @click="opentwo(1)">
      </div>
      <div class='icon' >
        <img v-if="showWD" src="../../assets/image/文档我不喜欢.png" alt="" class='catalogueIcontwo' @click="isLike()">
        <img v-if="!showWD" src="../../assets/image/文档我喜欢.png" alt="" class='catalogueIcontwo' @click="isLike()">
      </div>
    </div>
    <div class='numberMess' v-if="!isShowBottom" :class="{colorClassThree: !isColor}">
      <div class="bjNumber" v-if="isColor" @click="showBj" v-show="oneCount > 0">{{oneCount}}个笔记</div>
      <div class="bjNumber" style="color:#6A6E72;" v-if="!isColor" @click="showBj" v-show="oneCount > 0">{{oneCount}}个笔记</div>
      <div class='pagesNumber' v-if="isColor"><span style="color:#6E747E"  >{{paginationTwo}}</span><span  style="color:#6E747E" >/</span><span style="color:#9FA7AF" >{{totalPage}}</span></div>
      <div class='pagesNumber' v-if="!isColor"><span style="color:#6E747E"  >{{paginationTwo}}</span><span  style="color:#6E747E" >/</span><span style="color:#5A5A5B" >{{totalPage}}</span></div>
    </div>
    <div class='box' @click="close()">

    </div>
    <div class='catalogue' >
      <div class='catalogueTitle'><span>{{nameMes}}</span></div>
      <div class='catalogueList'>
        <div v-if="showMes" v-for="(item,index) in bookList" :key='item.list' @click="chapter(index)" :class='{chapterList:true,chapterClick:contentNum == index}' style="display: flex;width: 100%;align-items: flex-start;flex-direction: column;height:0.94rem;justify-content: center;">
          <div style="display: flex;" :id="item.cataNum">
            <span class="text-study-catalogue-mes" style="padding:0.2rem 0 0.18rem 0">第{{item.cataNum}}章</span>
            <span class='chapterName' style="">{{item.catalogue}}</span>
          </div>
        </div>
        <div v-if="!showMes" v-for="(item) in bookListTwo" :key='item.list' @click="chapterBj(item)" :class='{chapterList:true,chapterClick:contentNum + 1 == item.cataNum}' style="display: flex;width: 100%;align-items: flex-start;flex-direction: column;">
          <div style="display: flex;margin-bottom:0.3rem;" :id="item.cataNum">
            <span class="text-study-catalogue-mes" style="padding:0.2rem 0 0.18rem 0">第{{item.cataNum}}章</span>
            <span class='chapterName' style="">{{item.catalogue}}</span>
          </div>
            <el-timeline :reverse="reverse">
              <el-timeline-item
                v-for="(activity, index) in item.selfNotesList"
                :key="index"
                >
                <span style="color:#6A6E72;font-size:0.28rem;line-height:0.35rem">{{activity.noteContent}}</span>
              </el-timeline-item>
            </el-timeline>
        </div>
      </div>
      <div class="catalogue-bottom">
        <div class="fontshow"  :class="{showColor:isOne}" @click="isOneSeclec(0)">目录</div>
        <div style="width:0.02rem;height:0.34rem;background:rgba(186,191,197,0.45);"></div>
        <div class="fontshow" :class="{showColor:!isOne}" @click="isOneSeclec(1)">笔记</div>
      </div>
    </div>
     <div class="bjList" >
      <div class='bjList-head' @click="closeDtkBox()"></div>
      <div class="bjList-message">
        <div class="bjList-message-content" :class="{colorClass: !isColor}">
          <div v-for="(item,index) in bjList" :key="index" style="margin-top:0.08rem">
            <div class="bjList-mes">
              <div>{{item.createTime}}</div>
              <div>
                <img @click="edit(item)" style="width:0.34rem;height:0.34rem;margin-right: 0.3rem;" src="../../assets/image/编辑@2x.png" >
                <img style="width:0.34rem;height:0.34rem;" src="../../assets/image/删除(1)@2x.png" @click="del(item)">
              </div>
            </div>
            <div class="bjList-noteContent">{{item.noteContent}}</div>
          </div>
        </div>
        <div :class="{colorClass: !isColor}" style="width: 100%;display: flex;justify-content: start;align-items: center;height: 1rem;">
          <img style="width:0.33rem;height:0.33rem;position: absolute;margin-left:0.34rem" src="../../assets/image/记录@2x.png">
          <span style="font-size:0.28rem;color:rgba(159,167,175,0.8);margin-left:0.81rem;" @click="editAdd()"> 记笔记...</span>
        </div>
      </div>

    </div>
    <!-- 确认弹出框 - 提示错误信息 -->
    <IsBox :dialogVisible="IsBoxDialogVisible" :mess="mess"  @inNesclose="IsBoxcloseVerDialogMethod"></IsBox>
    <!-- 加载页面 -->
    <Loading :dialogVisible="verDialogVisible"></Loading>
     <!-- 添加笔记 -->
    <AddBj :dialogVisible="AddBjDialogVisible"  :messAll="messAll" :isColor="isColor" :isEdit="isEdit" @close='colseMes' @closeAll='colseAllMes'></AddBj>
     <!-- 夜间模式添加笔记 -->
    <AddBjBlack :dialogVisibleBlack="AddBjDialogVisibleBlack"  :messAll="messAll" :isColor="isColor" :isEdit="isEdit" @close='colseMes' @closeAll='colseAllMes'></AddBjBlack>
  </div>
</template>

<script>
import API from '@/api/api'
import $ from 'jquery'
import Loading from './../components/loading'
import AddBj from './../components/addBj'
import IsBox from './../components/isBox'
import AddBjBlack from './../components/addBjBlack'
export default {
  name: 'index',
  components: { Loading, AddBj, IsBox, AddBjBlack },
  data () {
    return {
      reverse: false,
      tenfenzhong: 0,
      // isChapter:0,
      specialCode: '',
      isShowBottom: true, // 是否显示底下的目录
      bookList: [], // 目录
      pagination: 1, // 当前页
      paginationTwo: 1, // 新当前页
      content: '', // 内容
      nameMes: '', // 书名
      lastPagination: 0, // 总页数
      contentNum: 0, // 阅读章节
      trainMateId: '', // 书id
      nextPagination: '', // 每一章的页数
      allPagination: [], // 所有章的页数集合
      totalPage: 0,
      readPage: 0,
      partPagination: [], // 已读页数集合
      isClose: true,
      startTime: 0,
      endTime: 0,
      paginationAll: 0,
      proportion: '', // 进度
      allWith: 0, // 所有数据的总宽度
      startWidth: 0, // 每一个页面的宽度
      isOver: false,
      allMessage: '',
      allLine: 0, // 一个页面有多少行
      readMess: '', // 看过的字
      lastWidth: 331, // 上次屏幕宽度
      lastHeight: 625.781, // 上次屏幕高度
      lastPage: 0, // 上次页号
      verDialogVisible: true,
      isSwiper: true, // 是否可以滑动
      allMes: '',
      showTy: true,
      isColor: true,
      isStatusBar: false, // 状态栏
      oneCount: 0, // 每一页的笔记数
      bjList: [], // 每一页的笔记集合
      AddBjDialogVisible: false,
      AddBjDialogVisibleBlack: false,
      objBj: {}, // 选择编辑的笔记对象
      messAll: '', // 编辑对象的内容
      isEdit: 0, // 0:添加 1：修改
      isshowFb: false,
      isOne: true, // 显示目录或者笔记
      showWD: true, // 文档是否喜欢
      mlorbj: 0, // 目录或者笔记
      showMes: true, // 展示目录true ， 展示笔记false
      bookListTwo: [], // 笔记目录集合
      IsBoxDialogVisible: false, // 是否打开提示信息
      mess: '' // 提示信息
    }
  },
  created () {
    this.totalPage = 0
    this.readPage = 0
    this.partPagination = []
    this.allPagination = []
    this.trainMateId = this.$route.params.id// 资料id
    this.specialCode = this.$route.params.specialCode
    this.startTime = new Date().getTime()
    this.findFavoriteByMateId()
    this.getselectMaterials()
    // this.getselectMaterialsTwo();
    if (localStorage.getItem('text-study.showTy') !== '' && localStorage.getItem('text-study.showTy') != null) {
      if (localStorage.getItem('text-study.showTy') === 'true') {
        this.showTy = false
      } else {
        this.showTy = true
      }
    }
  },
  mounted () {
    $('.wrapper').on('touchstart', function (e) {
      e.preventDefault()
    })
    // 监听返回键
    document.addEventListener('backbutton', this.onBackKeyDown, false)
    this.tenfen()
    if (localStorage.getItem('text-study.item') !== '' && localStorage.getItem('text-study.item') != null) {
      this.opentwo(Number(localStorage.getItem('text-study.item')))
    }
    // $('.wrapper').click(function(){
    //   that.isShowBottom = !that.isShowBottom;
    // })
  },
  methods: {
    // 出现文件错误返回上一级
    IsBoxcloseVerDialogMethod () {
      this.IsBoxDialogVisible = false
      // this.$router.push('/item_bank');
      this.$router.back()
    },
    changeType () {
      // alert(this.isShowBottom)
      this.isShowBottom = !this.isShowBottom
    },
    /**
         * 获取笔记目录
         */
    findAllSelfNotes () {
      API.post('findAllSelfNotes', {
        trainMateId: this.trainMateId
      }, 'json', (data, err) => {
        if (data.success) {
          this.bookListTwo = data.data
          setTimeout(() => {
            document.getElementById(this.contentNum + 1).scrollIntoView()
          }, 100)
        }
      })
    },
    // 返回键
    onBackKeyDown () {
      this.back()
    },
    // 查询该文档是否标记了喜欢
    findFavoriteByMateId () {
      API.post('findFavoriteByMateId', {
        trainMateId: this.trainMateId
      }, 'json', (data, err) => {
        if (data.success) {
          if (data.data === 1) {
            this.showWD = false
          } else {
            this.showWD = true
          }
        }
      })
    },
    // 是否喜欢的点击
    isLike () {
      this.showWD = !this.showWD
    },
    tenfen () {
      setTimeout(() => {
        this.tenfenzhong = this.tenfenzhong + 1
        this.tenfen()
        // console.log(this.tenfenzhong)
      }, 1000)
    },
    addSelfCredits () {
      API.post('addSelfCredits', {
        specialCode: this.specialCode,
        creditsId: 'learnTime'
      }, 'json', (data, err) => {
        if (data.success) {
          console.log(data)
        }
      })
    },
    isOneSeclec (item) {
      this.mlorbj = item
      if (item === 0) {
        this.isOne = true
        this.showMes = true
      } else {
        this.isOne = false
        this.findAllSelfNotes()
        this.showMes = false
      }
    },
    openMes () {
      $('.input-mes').find('textarea').focus()
    },
    // 删除笔记
    del (item) {
      API.post('deleteSelfNotes', {
        id: item.id
      }, 'json', (data, err) => {
        if (data.success) {
          this.selectSelfNotes()
        }
      })
    },
    edit (item) {
      this.objBj = item
      this.messAll = item.noteContent
      this.isEdit = 1
      this.isshowFb = true
      $('.bjList').css('display', 'none')
      this.isShowBottom = !this.isShowBottom
      $('.el-textarea').find('textarea').focus()
      if (!this.isColor) {
        this.AddBjDialogVisibleBlack = true
      } else {
        this.AddBjDialogVisible = true
      }
    },
    editAdd () {
      this.isEdit = 0
      $('.bjList').css('display', 'none')
      this.AddBjDialogVisible = true
    },
    // 关闭笔记列表
    closeDtkBox () {
      $('.bjList').css('display', 'none')
    },
    // 展示笔记
    showBj () {
      if (this.oneCount > 0) {
        $('.bjList').css('display', 'block')
      }
    },
    colseAllMes (data) {
      this.messAll = data
      this.isShowBottom = false
      if (!this.isColor) {
        this.AddBjDialogVisibleBlack = false
      } else {
        this.AddBjDialogVisible = false
      }
    },
    colseMes (data) {
      this.messAll = data
      if (this.messAll.trim() === '') {
        return
      }
      if (this.isEdit === 0) {
        API.post('addSelfNotes', {
          trainMateId: this.trainMateId,
          schedule: Math.round((this.paginationTwo / this.totalPage) * 100),
          noteContent: this.messAll,
          cataNum: this.contentNum
        }, 'json', (data, err) => {
          if (data.success) {
            this.selectSelfNotes()
            this.isShowBottom = false
            this.messAll = ''
            this.AddBjDialogVisibleBlack = false
            this.AddBjDialogVisible = false
          }
        })
        return
      }

      this.AddBjDialogVisible = false
      API.post('updateSelfNotes', {
        id: this.objBj.id,
        noteContent: this.messAll
      }, 'json', (data, err) => {
        if (data.success) {
          this.selectSelfNotes()
          this.isShowBottom = false
          this.messAll = ''
          this.AddBjDialogVisibleBlack = false
          this.AddBjDialogVisible = false
        }
      })
    },
    // 展示记笔记
    jbj () {
      this.isEdit = 0
      if (!this.isColor) {
        this.AddBjDialogVisibleBlack = true
        return
      }
      this.AddBjDialogVisible = true
    },
    // //获取内容
    // getselectMaterials(index){
    //   this.trainMateId = this.$route.params.id;
    //   this.nameMes = this.$route.params.name;
    //   API.post('selectLastContent', {
    //     trainMateId:this.trainMateId
    //   }, 'json',(data,err)=>{
    //     if(data.success){
    //       this.allMes = data.data;
    //       this.bookList = data.data.catalogueList;
    //       this.startTime = new Date().getTime();
    //       localStorage.setItem('textStudyStartTime',this.startTime)
    //       this.content = data.data.trainContentList;
    //       this.getContent(index);
    //     }
    //   })
    // },
    // 获取内容
    getselectMaterialsTwo () {
      // this.content = [];
      // this.allMes = '';
      this.bookList = []
      this.trainMateId = this.$route.params.id
      this.nameMes = this.$route.params.name
      API.post('selectLastContent', {
        trainMateId: this.trainMateId
      }, 'json', (data, err) => {
        if (data.success) {
          // this.allMes = data.data;
          this.bookList = data.data.catalogueList
          // this.content = data.data.trainContentList;
        }
      })
    },
    // 获取笔记
    selectSelfNotes () {
      API.post('selectSelfNotes', {
        trainMateId: this.trainMateId,
        schedule: Math.round((this.paginationTwo / this.totalPage) * 100)
      }, 'json', (data, err) => {
        if (data.success) {
          this.oneCount = data.data.length
          this.bjList = []
          this.bjList = data.data
          for (var i = 0; i < data.data.length; i++) {
            this.bjList[i].createTime = this.GLOBAL.timestampToTime(data.data[i].createTime)
          }
          if (this.bjList.length < 1) {
            $('.bjList').css('display', 'none')
          }
        }
      })
    },
    // 获取内容
    getselectMaterials (index) {
      this.trainMateId = this.$route.params.id
      this.nameMes = this.$route.params.name
      API.post('selectLastContent', {
        trainMateId: this.trainMateId
      }, 'json', (data, err) => {
        if (data.success) {
          this.allMes = data.data
          this.bookList = data.data.catalogueList
          // localStorage.setItem('textStudyStartTime',this.startTime)
          this.content = data.data.trainContentList
          this.oneCount = data.data.selfNotesList.length
          for (var i = 0; i < data.data.selfNotesList.length; i++) {
            data.data.selfNotesList[i].createTime = this.GLOBAL.timestampToTime(data.data.selfNotesList[i].createTime)
          }
          this.bjList = data.data.selfNotesList
          this.getContent(index)
        }
      })
    },
    getContent (index) {
      if (index != null) {
        this.pagination = 1
      } else {
        this.pagination = this.allMes.lastContent.lastPage
      }
      this.paginationTwo = this.allMes.lastContent.lastPage
      if (index != null) {
        this.contentNum = index
      } else {
        this.contentNum = this.allMes.lastContent.trainCatalogue.cataNum - 1
      }
      if (!this.content[this.contentNum].content) {
        this.mess = '文件错误，请联系管理员'
        this.IsBoxDialogVisible = true
        return
      }
      $('.columnContainer')[0].innerHTML = this.content[this.contentNum].content.replace(/\\n/g, "</br><span style='width:0.722rem;display:inline-block'></span>")
      if (index != null) {
        $('.columnTitle')[0].innerHTML = this.bookList[this.contentNum].catalogue
        $('.columnChapter')[0].innerHTML = '第' + this.bookList[this.contentNum].cataNum + '章'
      } else {
        $('.columnTitle')[0].innerHTML = this.allMes.lastContent.trainCatalogue.catalogue
        $('.columnChapter')[0].innerHTML = '第' + this.allMes.lastContent.trainCatalogue.cataNum + '章'
      }

      var screenWidth = window.screen.availWidth + 'px'
      $('.column').css('columnWidth', screenWidth)
      this.lastPagination = Math.round($('.column')[0].scrollWidth / window.screen.availWidth)

      if (index == null) {
        // 获取章节的总页数
        for (var i = 0; i < this.content.length; i++) {
          $('.columnContainer2')[0].innerHTML = this.content[i].content.replace(/\\n/g, "</br><span style='width:0.722rem;display:inline-block'></span>")
          // $('.columnTitle2')[0].innerHTML = this.bookList[i].catalogue;
          // $('.columnChapter2')[0].innerHTML = '第'+this.bookList[i].cataNum+'章';
          $('.column2').css('columnWidth', screenWidth)
          this.nextPagination = Math.round($('.column2')[0].scrollWidth / window.screen.availWidth)
          this.allPagination.push(this.nextPagination)
          this.totalPage += this.allPagination[i]
        }
        if (this.contentNum > 0) {
          for (var j = 0; j < this.contentNum; j++) {
            this.paginationTwo = Number(this.allPagination[j]) + Number(this.paginationTwo)
          }
        } else {
          this.paginationTwo = this.allMes.lastContent.lastPage
        }
      } else {
        this.paginationTwo = 1
        if (this.bookList[this.contentNum].cataNum === 1) {
          this.paginationTwo = 1
        } else {
          for (var k = 0; k < this.bookList[this.contentNum].cataNum - 1; k++) {
            this.paginationTwo = Number(this.paginationTwo) + Number(this.allPagination[k])
          }
          // this.paginationTwo += 1;
        }
      }

      var column = document.getElementById('column')
      column.style.transform = 'translateX(' + (this.pagination - 1) * (-window.screen.availWidth) + 'px' + ')'

      this.clostLoading()
    },
    clostLoading () {
      const that = this
      setTimeout(function () { that.verDialogVisible = false }, 1000)
    },
    handleTouchStart () {
      alert('111')
    },
    swiperleft: function () {
      this.messAll = ''
      var column = document.getElementById('column')
      if ($('.box').css('display') === 'block') {
        return
      }
      if (this.contentNum < this.content.length - 1) {
        if (this.pagination >= this.lastPagination) {
          this.contentNum++
          column.style.transform = 'translateX(' + (-1) * (-window.screen.availWidth) + 'px' + ')'
          $('.column').css('transition', '0s')
          $('.columnContainer')[0].innerHTML = this.content[this.contentNum].content.replace(/\\n/g, "</br><span style='width:0.722rem;display:inline-block'></span>")
          $('.columnTitle')[0].innerHTML = this.bookList[this.contentNum].catalogue
          $('.columnChapter')[0].innerHTML = '第' + this.bookList[this.contentNum].cataNum + '章'
          var screenWidth = window.screen.availWidth + 'px'
          $('.column').css('columnWidth', screenWidth)
          this.lastPagination = Math.round($('.column')[0].scrollWidth / window.screen.availWidth)
          this.pagination = 1
          this.paginationTwo++
          column.style.transform = 'translateX(' + (this.pagination - 1) * (-window.screen.availWidth) + 'px' + ')'
          $('.column').css('transition', '0.5s')
        } else {
          ++this.pagination
          ++this.paginationTwo
          column.style.transform = 'translateX(' + (this.pagination - 1) * (-window.screen.availWidth) + 'px' + ')'
          $('.column').css('transition', '0.5s')
        }
      } else {
        if (this.pagination < this.lastPagination) {
          ++this.pagination
          ++this.paginationTwo
          column.style.transform = 'translateX(' + (this.pagination - 1) * (-window.screen.availWidth) + 'px' + ')'
          $('.column').css('transition', '0.5s')
        } else {
          this.$toast('已是最后一页')
        }
      }
      this.isShowBottom = false
      this.selectSelfNotes()
    },
    swiperright: function () {
      var column = document.getElementById('column')
      $('.mint-toast').css('display', 'none')
      this.messAll = ''
      // this.readSum();
      if ($('.box').css('display') === 'block') {
        return
      }
      if (this.contentNum === 0) {
        if (this.pagination > 1) {
          this.pagination--
          this.paginationTwo--
          column.style.transform = 'translateX(' + (this.pagination - 1) * (-window.screen.availWidth) + 'px' + ')'
          $('.column').css('transition', '0.5s')
        }
      } else {
        if (this.pagination > 1) {
          this.pagination--
          this.paginationTwo--
          column.style.transform = 'translateX(' + (this.pagination - 1) * (-window.screen.availWidth) + 'px' + ')'
          $('.column').css('transition', '0.5s')
        } else {
          this.contentNum--
          column.style.transform = 'translateX(' + (this.allPagination[this.contentNum]) * (-window.screen.availWidth) + 'px' + ')'
          $('.column').css('transition', '0s')
          $('.columnContainer')[0].innerHTML = this.content[this.contentNum].content.replace(/\\n/g, "</br><span style='width:0.722rem;display:inline-block'></span>")
          $('.columnTitle')[0].innerHTML = this.bookList[this.contentNum].catalogue
          $('.columnChapter')[0].innerHTML = '第' + this.bookList[this.contentNum].cataNum + '章'
          var screenWidth = window.screen.availWidth + 'px'
          $('.column').css('columnWidth', screenWidth)
          this.lastPagination = Math.round($('.column')[0].scrollWidth / window.screen.availWidth)
          this.pagination = this.lastPagination
          this.paginationTwo--
          column.style.transform = 'translateX(' + (this.pagination - 1) * (-window.screen.availWidth) + 'px' + ')'
          $('.column').css('transition', '0.5s')
        }
      }

      this.isShowBottom = false
      this.selectSelfNotes()
    },
    // readSum(){
    //   if(this.contentNum >= 1){
    //     for(var j = 0 ; j <  this.contentNum ; j++){
    //       this.partPagination.push(this.allPagination[j])
    //     }
    //     this.partPagination.push(Number(this.pagination))
    //   }else{
    //     this.readPage = this.pagination;
    //   }
    //   for(var k = 0 ; k < this.partPagination.length ; k++){
    //     this.readPage += Number(this.partPagination[k])
    //   }
    // },
    // 返回
    back () {
      $('.mint-toast').css('display', 'none')
      // if(this.contentNum >= 1){
      //   for(var j = 0 ; j <  this.contentNum ; j++){
      //     this.partPagination.push(this.allPagination[j])
      //   }
      //   this.partPagination.push(Number(this.pagination))
      // }else{
      //   this.readPage = this.pagination;
      // }
      // for(var k = 0 ; k < this.partPagination.length ; k++){
      //   this.readPage += Number(this.partPagination[k])
      // }
      if (this.startTime === 0) {
        this.endTime = 0
      } else {
        this.endTime = new Date().getTime()
      }
      // var aa = this.bookList;
      // var shedu = Math.round((this.readPage / this.totalPage) * 100);
      // if(shedu > 100){
      //   shedu = 100;
      // }
      // 告诉接口是否喜欢
      this.isLikeBook()
      API.post('save', {
        contentId: this.bookList[this.contentNum].id,
        pagination: this.pagination,
        trainMateId: this.trainMateId,
        proportion: this.proportion,
        studyTime: this.endTime - this.startTime
      }, 'json', (data, err) => {
        if (data.success) {
          this.$router.back()
        } else {
          this.$router.back()
        }
      })
    },
    isLikeBook () {
      var isLikeMes = 0
      if (this.showWD) {
        isLikeMes = 0
      } else {
        isLikeMes = 1
      }
      API.post('addFavorite', {
        trainMateId: this.trainMateId,
        favo: isLikeMes
      }, 'json', (data, err) => {

      })
    },
    // 关闭目录
    close () {
      $('.box').css('display', 'none')
      $('.catalogue').css('display', 'none')
      this.getselectMaterialsTwo()
    },
    open (item) {
      $('.mint-toast').css('display', 'none')
      if (this.mlorbj === 0) {
        // this.getselectMaterialsTwo();
      } else {
        this.findAllSelfNotes()
      }
      $('.box').css('display', 'block')
      $('.catalogue').css('display', 'block')

      if (item === 1) {
        $('.catalogueTitle').css('background', 'rgb(22, 22, 23)')
        $('.catalogue').css('background', '#000000')
        $('.catalogueTitle').css('color', '#6A6E72')
        $('.catalogueList').css('background', 'rgb(22, 22, 23)')
        $('.chapterName').css('background', '')
        $('.text-study-catalogue-mes').css('background', '')
        $('.chapterList').css('border-bottom', '0.012rem solid  rgba(106,110,114,0.45)')
        $('.bottomShow').css('borderTopColor', 'rgba(242,242,242,0.1)')
        $('.bottomShow').css('background', '#161617')
        $('.column').css('color', '#6A6E72')
        $('.styleHead').css('color', '#6A6E72')
        $('.jbjClass').css('background', '#161617')
        $('.jbjClass').css('color', '#6A6E72')
        $('.bjNumber').css('color', '#6A6E72')
        $('.catalogue-bottom').css('background', 'rgb(22, 22, 23)')
        this.isStatusBar = true
        // setTimeout(() => {
        document.getElementById(this.contentNum).scrollIntoView()
        // }, 700)

        return
      }
      $('.catalogueTitle').css('background', '#fff')
      $('.catalogueList').css('background', 'white')
      $('.catalogue').css('background', 'white')
      $('.catalogueTitle').css('color', '#4E4E4E')
      $('.catalogueList').css('background', 'white')
      $('.chapterName').css('background', 'white')
      $('.text-study-catalogue-mes').css('background', 'white')
      $('.chapterList').css('border-bottom', '0.012rem solid rgba(231,236,244,,0.45)')
      $('.bottomShow').css('borderTopColor', 'rgba(221, 221, 221, 1)')
      $('.column').css('color', '#6E747E')
      $('.styleHead').css('color', '#4E4E4E')
      $('.jbjClass').css('background', '#fff')
      $('.jbjClass').css('color', '#6E747E')
      $('.bjNumber').css('color', '#fff')
      $('.catalogue-bottom').css('background', '#fff')
      this.isStatusBar = false
      // setTimeout(() => {
      document.getElementById(this.contentNum).scrollIntoView()
      // }, 700)
    },
    opentwo (item) {
      localStorage.setItem('text-study.item', item)
      this.showTy = !this.showTy
      localStorage.setItem('text-study.showTy', this.showTy)
      if (item === 0) {
        $('.header').css('background', '#000000')
        $('.header').css('borderWidth', '0rem')
        $('.touchConteiner').css('background', '#000000')
        $('.catalogueTitle').css('color', '#6A6E72')
        $('.columnTop').css('color', '#6A6E72')
