        $('.catalogue').css('background', '#000000')
        $('.catalogueList').css('background', '#000000')
        // $('.chapterName').css('background','#000000');
        // $('.text-study-catalogue-mes').css('background','#000000');
        $('.chapterList').css('border-bottom', '0.012rem solid rgba(106,110,114,0.45)')
        $('.bottomShow').css('borderTopColor', 'rgba(242,242,242,0.1)')
        $('.bottomShow').css('background', '#161617')
        $('.column').css('color', '#6A6E72')
        $('.styleHead').css('color', '#6A6E72')
        // $('.jbjClass').css('background','#161617');
        // $('.jbjClass').css('color','#6A6E72');
        $('.bottomShow').css('borderTopColor', 'rgba(242,242,242,0.1)')
        $('.jbjClass').css('background', '#161617')
        $('.jbjClass').css('color', '#6A6E72')
        // $('.jbjClass').css('borderTopColor','rgba(242,242,242,0.1)');

        $('.bjNumber').css('color', '#6A6E72')
        this.isColor = false
        this.isStatusBar = true
        return
      }
      $('.header').css('background', '#fff')
      $('.header').css('borderWidth', '0.012rem')
      $('.touchConteiner').css('background', '#fff')
      $('.bottomShow').css('background', '#fff')
      $('.columnTop').css('color', '#9FA7AF')
      $('.catalogueTitle').css('background', '#fff')
      $('.catalogueList').css('background', '#fff')
      $('.catalogue').css('background', '#fff')
      $('.catalogueTitle').css('color', '#4E4E4E')
      $('.chapterName').css('background', '#fff')
      $('.text-study-catalogue-mes').css('background', '#fff')
      $('.chapterList').css('border-bottom', '0.012rem solid rgba(231,236,244,1)')
      $('.bottomShow').css('borderTopColor', 'rgba(221, 221, 221, 1)')
      $('.column').css('color', '#6E747E')
      $('.styleHead').css('color', '#4E4E4E')
      $('.jbjClass').css('background', '#fff')
      $('.jbjClass').css('color', '#6E747E')
      $('.bjNumber').css('color', '#fff')
      $('.bottomShow').css('border-top', '0.01rem solid #E7ECF4')
      this.isColor = true
      this.isStatusBar = false
    },
    chapter (index) {
      this.paginationTwo = 1
      this.contentNum = index
      this.pagination = 1
      $('.box').css('display', 'none')
      $('.catalogue').css('display', 'none')
      this.getContent(index)
      $('.column').css('transition', '0s')
      this.selectSelfNotes()
    },
    chapterBj (index) {
      this.paginationTwo = 1
      this.contentNum = index.cataNum - 1
      this.pagination = 1
      $('.box').css('display', 'none')
      $('.catalogue').css('display', 'none')
      this.getContent(index.cataNum - 1)
      $('.column').css('transition', '0s')
      this.selectSelfNotes()
    }
  },
  // 消除返回键监听
  beforeDestroy () {
    document.removeEventListener('backbutton', this.onBackKeyDown, false)
  },
  watch: {
    tenfenzhong (val) {
      if (val !== this.startTime) {
        if (val % 600 === 0) {
          this.addSelfCredits()
        }
      }
    },
    isStatusBar (val) {
      if (val === false) {
        // eslint-disable-next-line no-undef
        StatusBar.backgroundColorByHexString('#fff')
        // eslint-disable-next-line no-undef
        StatusBar.styleDefault()
      } else {
        // eslint-disable-next-line no-undef
        StatusBar.backgroundColorByHexString('#000')
        // eslint-disable-next-line no-undef
        StatusBar.styleLightContent()
      }
    },
    paginationTwo (val, oldval) {
      if (this.contentNum >= 1) {
        for (var j = 0; j < this.contentNum; j++) {
          this.partPagination.push(this.allPagination[j])
        }
        this.partPagination.push(Number(this.pagination))
      } else {
        this.readPage = this.pagination
      }
      for (var k = 0; k < this.partPagination.length; k++) {
        this.readPage += Number(this.partPagination[k])
      }
      this.proportion = Math.round((this.paginationTwo / this.totalPage) * 100)
      if (this.proportion > 100) {
        this.proportion = 100
      }
      localStorage.setItem('contentId', this.bookList[this.contentNum].id)
      localStorage.setItem('pagination', this.pagination)
      localStorage.setItem('trainMateId', this.trainMateId)
      localStorage.setItem('proportion', this.proportion)
    }

  },
  messAll (val, oldval) {
    if (val.trim() !== '') {
      $('.span-mes').show()
    } else {
      $('.span-mes').hide()
    }
  }
}
</script>

