<template>
  <el-dialog
    class="verTwo"
    :visible="bjDialogVisible"
    :modal-append-to-body="false"
    :close-on-click-modal="true"
    @close="kcDialog"
    top="0"
    @opened="openMes"
    :center="true"
    :show-close="false">
    <span style="color:red;font-size:0.3rem;"></span>
    <!-- <img style="width:0.33rem;height:0.33rem;position: absolute;margin-left:0.34rem;top: 0;margin-top: 0.35rem;" src="../../assets/image/记录@2x.png">  -->
      <el-input
        class="input-mes"
        type="textarea"
        :rows="1"
        maxlength="100"
        placeholder="记笔记..."
        v-model="messAllNew"
        :autosize='true'
        name="customerInput">
      </el-input>
      <span class="span-mes" v-if="messAllNew.length > 0"   @click="fb()">发表</span>

  </el-dialog>
</template>

<script>
import $ from 'jquery'
import { debuglog } from 'util';
export default {
  props: {
    bjDialogVisible: {
      type: Boolean,
      required: true
    },
    messAll: {
      type: String,
      default: ''
    },
    isEdit: {
      type: Number,
      default: 0
    },
    showTy: {
      type: Boolean,
      required: true
    }

  },
  data () {
    return {
      isShowFb: false,// 是否显示发表按钮
      messAllNew : '',
    }
  },
  mounted () {
    
  },
  create () {

  },
  methods: {
    openMes () {
    //   console.log("jjj");
    // console.log($('.input-mes').find('textarea'));
  　
      let u = navigator.userAgent // 判断是安卓还是ios
      let isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1 // g
      let isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/) // ios终端
      if (isIOS) {
        //cordova.plugins.Keyboard.hideKeyboardAccessoryBar(false);
        $('.input-mes').find('textarea').on('focus', function () {
 
            var target = this;
            setTimeout(function(){
              console.log("jjj");
                target.scrollIntoViewIfNeeded();
                // target.scrollIntoView(true);
                // console.log('scrollIntoViewIfNeeded');
            },200);
        });
      }
      
      this.messAllNew = this.messAll;
    },
    kcDialog () {
      if (this.isEdit === 1) {
        this.messAllNew = '';
      }
      this.$emit('closeAll', this.messAllNew)
    },
    fb () {
      this.$emit('close', this.messAllNew)
      this.messAllNew = ''
    },
    back () {
      $('.bigBox').css('display', 'flex')
    },
    cancel () {
      $('.bigBox').css('display', 'none')
    },
    comfirm () {
      this.messAllNew = ''
      this.$emit('closeAll', this.messAllNew)
    }
  },
  // watch: {
  //   messAll (newval, oldval) {
  //     if (this.GLOBAL.filterSpace(newval)) {
  //       this.messAll = oldval
  //     }
  //     if (newval.trim() !== '') {
  //       this.isShowFb = true
  //     } else {
  //       this.isShowFb = false
  //     }
  //   }
  // }
}
</script>

<style lang="less">
  .verTwo .el-dialog__wrapper{
    position: absolute !important;
    top:0 !important;
    left:0;
  }
  .verTwo .el-dialog{
    position: relative;
    width:100%;
    min-height:1.08rem;
    position: absolute;
    bottom: 0;
    left:0;
    margin: 0!important;
  }
  .verTwo .el-dialog__header{
    padding:0;
  }
  .verTwo{
    width:100%;
    height:100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top:0;
    left:0;
  }
  .verTwoMes{
    background: rgb(22, 22, 23);
  }
  .verTwo .el-dialog__body{
    position: relative;
    width:100%;
    min-height:1.08rem;
    text-align: center;
    display: flex;
    align-items: center;
  }
  .verTwo .el-dialog--center .el-dialog__body{
    padding: 0 !important;
  }
  .verTwo .input-mes{
    // height: 1.08rem;
    display: flex;
    width: calc(100% - 1.5rem);
    font-size: 0.3rem;
    outline: none!important;
    border: 0!important;
    color:rgba(159,167,175,1);
    margin-left: 0.5rem;
    word-break: break-word;
  }
  .verTwo .span-mes{
    position: absolute;
    right: 0.36rem;
    color: #55CC98;
    font-size:0.28rem;
    bottom: 0.4rem;
  }
  .verTwo input::-webkit-input-placeholder {
      font-size:0.28rem;
      color:rgba(159,167,175,0.8);
  }
  .verTwo .el-textarea__inner{
    border:none;
    padding: 0 0.15rem 0.05rem 0.15rem !important;
    border-radius:0!important;
  }
  .verTwo .bigBox{
    z-index: 99;
    width:100%;
    height:100%;
    background:rgba(0,0,0,0.7);
    position: fixed;
    top:0;
    display: none;
    justify-content: center;
    align-items: center;
  }
  .verTwo  .smallBox{
    position: relative;
    z-index: 999;
    width:5.5rem;
    height:2.74rem;
  }
  .smallBox img{
    width:5.5rem;
    height:2.74rem;
  }
  .verTwo .smallBox .boxWord{
    position:absolute;
    width:100%;
    display: flex;
    justify-content: center;
    text-align: center;
    top:0.56rem;
    left:0;
    z-index: 1000;
    font-size:0.34rem;

    letter-spacing:0.04rem;
  }
  .verTwo .startText-mess-two{
      width: 100%;
      height: 1rem;
      position: absolute;
      z-index: 999;
      display: flex;
      justify-content: space-around;
      align-content: center;
      bottom: 0;
      font-size: 0.34rem;
      border-top: 0.012rem solid #E7ECF4;
  }
  .verTwo .startText-mess-two-wait{
      width: 48%;
      height: 100%;
      color:rgba(110,116,126,1);
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 0.34rem;
  }
  .verTwo .startText-mess-two-go{
      width: 48%;
      height: 100%;
      color:rgba(85,204,152,1);
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 0.34rem;
  }
  .ch{
    width: 100%;
    background:rgba(255,255,255,1);
  }

</style>



